<template>
  <el-container id="auth-container">
        <el-aside width="200px" id="aside-container">
          <div>
            <div class="aside-list" @click="asideLight"><router-link to="/auth/login" :class="asideList.login"><span>图</span> 登录</router-link></div>
            <div class="aside-list" @click="asideLight"><router-link to="/auth/register" :class="asideList.register"><span>图</span> 注册</router-link></div>
<!--            <div class="aside-list"><span>图</span> 重置</div>-->
          </div>
        </el-aside>
        <el-main>
          <div id="auth-router-container">
            <transition enter-active-class='animated fadeInDown'
                        animate-delay="1s">
              <router-view> </router-view>
            </transition>
          </div>
        </el-main>
  </el-container>
</template>

<script>
export default {
    data(){
        return{
            asideList:{
                login:'active',
                register:'',
                reset:''
            }
        }
    },
    methods:{
        asideLight:function(){
            let name = this.$route.name
            if(name === 'login'){
                for(let i in this.asideList){
                    this.asideList[i] = ''
                }
                this.asideList.login = 'active'
            }else if(name === 'register'){
                for(let i in this.asideList){
                    this.asideList[i] = ''
                }
                this.asideList.register = 'active'
            }
        }
    },
    mounted() {
        this.asideLight()
    }
};
</script>

<style scoped>
  .fade-enter-active,.fade-leave-active{
    transition: opacity 0.5s;
  }
  .aside-list a{
      color:grey;
  }
  .aside-list .active{
      color:#fff;
  }
  #auth-container{
      height:100%
  }
  #aside-container a{
      text-decoration:none;
  }
  #aside-container>div>.aside-list{
      margin-bottom:16px;
  }

  .fade-enter,.fade-leave-active{
    opacity:0;
  }
  #auth-router-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    flex-wrap: wrap;
  }
  #aside-container{
    display:flex;
    justify-content: center;
    align-items: center;
  }
</style>
